    margin-left: 4px;
}

/* Color picker styles */
input[type="color"][data-v-0296c876] {
    aspect-ratio: 1/1;
    min-width: 32px;
    min-height: 32px;
    width: 32px;
    height: 32px;
    padding: 0!important;
    margin: 0!important;
    border-radius: 50%;
    box-shadow: 0 0 5px rgba(255, 255, 255, 0.5) !important;
    transition: transform 0.2s, box-shadow 0.2s;
	cursor: pointer!important;
}
input[type="color"][data-v-0296c876]:hover {
    transform: scale(1.1);
    transition: transform 0.2s, box-shadow 0.2s;
    box-shadow: 0 0 12px rgba(255, 255, 255, 0.5) !important;
}
input[type="color"][data-v-0296c876]::-webkit-color-swatch-wrapper {
    padding: 0;
}
