    border: none;
    border-radius: 50%;
}

/* Firefox support */
input[type="color"][data-v-0296c876]::-moz-color-swatch {
    border: none;
    border-radius: 50%;
}

/* Color reset button transitions */
.icon-reset-color[data-v-0296c876] {
    min-width: 32px;
    min-height: 32px;
    background-color: transparent;
    border: none;
    cursor: pointer;
