}
.icon-reset-color[data-v-0296c876]:hover {
    opacity: 1;
    background-color: var(--color-background-hover);
}
.fade-scale-enter-from[data-v-0296c876] {
    opacity: 0;
    transform: scale(0);
}
.fade-scale-enter-active[data-v-0296c876], 
.fade-scale-leave-active[data-v-0296c876] {
    transition: all 0.3s ease;
}
.fade-scale-enter-from[data-v-0296c876],
.fade-scale-leave-to[data-v-0296c876] {
    opacity: 0;
    transform: scale(0);
}
.icon-preview[data-v-0296c876] {
    display: inline-flex;
    align-items: center;
    margin: 0 4px;
}
#widget-icon[data-v-0296c876] {
    max-width: 350px!important;
}
